/*
* The following CSS modified from
* https://www.w3schools.com/howto/howto_css_switch.asp
*/


/* The switch - the box around the slider */
.switchTool {
  position: relative;
  display: inline-block;
  width: 150px;
  height: 65px;
  background-color: #3E4043;
  border-radius: 35px;
}

/* Hide default HTML checkbox */
.switchTool input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.sliderBack {
  display: inline-block;
  width: 150px;
  height: 65px;
  border-radius: 35px;
  position: relative;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #7F3AD7;
  transition: .7s;

  border-radius: 10px;
}

.checked {
-webkit-transform: translateX(85px);
  transform: translateX(85px);
}

.sliderFront {
  position: absolute;
  content: "";
  height: 50px;
  width: 50px;
  left: 8px;
  bottom: 8px;
  background-color: white;
  transition: .7s;
  content: attr(toolswitch);
  border-radius: 10px;
}

input:checked+.slider {
  background-color: #3E4043;
}

input:focus+.slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked+.slider:before {
  -webkit-transform: translateX(85px);
  transform: translateX(85px);
}

.toolP {
  display: flex;
  justify-content: space-between;
}

